* Styles shared between mobile and desktop\r\n */\r\n\r\n.logo {\r\n  fill: white;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n}","/**\r\n * Import all the fonts used in this project.\r\n */\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-Regular.otf\");\r\n    font-weight: normal;\r\n    font-style: normal;\r\n}\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularBold.otf\");\r\n    font-weight: bold;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularLight.otf\");\r\n    font-weight: 300;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularThin.otf\");\r\n    font-weight: 100;\r\n}","/**\r\n * Compiles all the styles together\r\n */\r\n\r\n// Shared all screens\r\n@import './common.scss';\r\n@import './constants.scss';\r\n@import './fonts.scss';\r\n\r\n// Desktop styles\r\n@media (min-width: $DESKTOP_WIDTH) {\r\n  @import './desktop/index.scss';\r\n  @import './desktop/components/navbar.scss';\r\n  @import './desktop/components/landing_page.scss';\r\n}\r\n\r\n// Mobile styles\r\n@media (max-width: $DESKTOP_WIDTH - 1) {\r\n  @import './mobile/index.scss';\r\n  @import './mobile/components/landing_page.scss';\r\n  @import './mobile/components/mobile_open_menu.scss';\r\n  @import './mobile/components/mobile_menu_line_list.scss';\r\n  @import './mobile/components/mobile_menu_nav_list.scss';\r\n}","/**\r\n * Styles for the main site\r\n */\r\n\r\nbody {\r\n  color: white;\r\n  background-color: black;\r\n  font-family: 'Sprat Type';\r\n  height: 100vh;\r\n  width: 100%;\r\n}\r\n\r\n.mobile {\r\n  display: none;\r\n}","/**\r\n * Landing page navbar\r\n * https://github.com/EthanNakache/Sprat-type\r\n */\r\n\r\n.navbar {\r\n  width: 1039px;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  padding: 12px 0 12px 0;\r\n\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: 141px;\r\n\r\n  .nav-circle {\r\n    background-color: rgba(255, 255, 255, .3);\r\n    height: 20px;\r\n    width: 20px;\r\n    border-radius: 50%;\r\n    border: 1px solid white;\r\n    transition: .5s;\r\n    cursor: pointer;\r\n\r\n    // When a nav circle is selected, we fill it in\r\n    &.selected {\r\n      background-color: white;\r\n    }\r\n  }\r\n\r\n  .lineLabel{\r\n    width: 50px;\r\n    font-size: 50px;\r\n  }\r\n}\r\n","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n// Constants\r\n$FONT_SIZE_LINK: 48px;\r\n$FONT_SIZE_LABEL: 26px;\r\n$FONT_SIZE_TITLE: 60px;\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  .link {\r\n    color: white;\r\n    text-decoration: none;\r\n    font-size: $FONT_SIZE_LINK;\r\n  }\r\n\r\n  #about {\r\n    position: absolute;\r\n    top: 25px;\r\n    left: 25px;\r\n\r\n  }\r\n\r\n  #people {\r\n    position: absolute;\r\n    bottom: 25px;\r\n    left: 25px;\r\n  }\r\n\r\n  #curr_line {\r\n    position: absolute;\r\n    font-size: 100px;\r\n    left: 20vw;\r\n    bottom: 45%;\r\n  }\r\n\r\n  #label {\r\n    position: absolute;\r\n    font-size: $FONT_SIZE_LABEL;\r\n    right: 50px;\r\n    bottom: 50px;\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: 24px;\r\n    right: 24px;\r\n    width: 61px;\r\n    height: 80px;\r\n  }\r\n}\r\n\r\n.title-theme {\r\n  position: absolute;\r\n  top: 24px;\r\n  font-size: $FONT_SIZE_TITLE;\r\n}","/**\r\n * Mobile styles in general for the site\r\n */\r\n\r\nhtml {\r\n  height: 100%;\r\n}\r\n\r\nbody {\r\n  color: white;\r\n  background-color: black;\r\n  font-family: 'Sprat Type';\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  #root {\r\n    height: 100%;\r\n  }\r\n}","\r\n// TODO: unsure why this font is so much smaller than figma\r\n$FONT_SIZE_TITLE: 48px;\r\n\r\n/**\r\n * 3D model in the landing page.\r\n */\r\n.landing-page-background {\r\n  z-index: -1;\r\n  position: fixed;\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n\r\n  &.mobile-line-menu-open {\r\n    top: 0px;\r\n    height: 234px;\r\n  }\r\n\r\n  &.mobile-nav-menu-open {\r\n    bottom: 0px;\r\n    height: 240px;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  &.mobile-line-menu-open {\r\n    position: static;\r\n  }\r\n\r\n  .title-theme {\r\n    position: fixed;\r\n    font-size: $FONT_SIZE_TITLE;\r\n    // Vertically stretch the font\r\n    transform: scale(1, 1.5);\r\n    transition-duration: 1s;\r\n    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n    &.mobile-line-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      transform: translateY(-246px) scale(1, 1.5);\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      bottom: 94px;\r\n    }\r\n  }\r\n\r\n  // Hide desktop elements\r\n  .link {\r\n    display: none;\r\n  }\r\n\r\n  .lineLabel {\r\n    display: none;\r\n  }\r\n\r\n  #label {\r\n    display: none;\r\n  }\r\n\r\n  #curr_line {\r\n    display: none;\r\n  }\r\n\r\n  .logo {\r\n    position: fixed;\r\n    bottom: 21px;\r\n    width: 69px;\r\n    height: 90px;\r\n\r\n    &.mobile-line-menu-open {\r\n      display: none;\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      display: none;\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Component on mobile that opens the menu\r\n */\r\n\r\n#mobile-open-menu {\r\n  position: fixed;\r\n  top: 34px;\r\n  width: 37px;\r\n  height: 37px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n\r\n  #mobile-open-menu-sign {\r\n    font-size: 40px;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  #mobile-open-menu-border {\r\n    position: absolute;\r\n    border: 1px solid white;\r\n    transform: rotate(45deg);\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the lines in the show\r\n */\r\n\r\n#mobile-menu-line-list {\r\n  position: fixed;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  transform: translateY($LINE_ELEMENT_HEIGHT*11);\r\n\r\n  &.mobile-line-menu-open {\r\n    position: absolute;\r\n    transform: translateY($LINE_ELEMENT_HEIGHT*7);\r\n  }\r\n\r\n  .line-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $LINE_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .line-element-body {\r\n      margin-top: 32px;\r\n\r\n      .title {\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n\r\n      .designers {\r\n        height: 16px;\r\n        margin-top: 16px;\r\n        font-size: 16px;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Constants for stylesheet\r\n */\r\n\r\n$DESKTOP_WIDTH: 768px;\r\n$LINE_ELEMENT_HEIGHT: 142px;\r\n$NAV_ELEMENT_HEIGHT: 126px;","/**\r\n * Styles for the menu that shows all the nav links\r\n */\r\n\r\n#mobile-menu-nav-list {\r\n  position: relative;\r\n  width: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  opacity: 0;\r\n  z-index: -2;\r\n\r\n  &.mobile-nav-menu-open {\r\n    opacity: 1;\r\n    z-index: 2;\r\n  }\r\n\r\n  .nav-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $NAV_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n    text-decoration: none;\r\n\r\n    &.end {\r\n      border-bottom: 1px solid white;\r\n    }\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .nav-element-body {\r\n      .title {\r\n        color: white;\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
